body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: linear-gradient(to right, rgb(255, 232, 232), rgb(234, 251, 255));
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.game-block {
    margin: 0;
    padding: 0;
    width: 1.2em;
    height: 1.2em;
    border: 1px solid #ddd;
}

p {
    margin: 2px;
}

.piece-i {
    background-color: #ff000d;
}

.piece-j {
    background-color: #0056c7;
}

.piece-l {
    background-color: #09ff00;
}

.piece-o {
    background-color: #ff0077;
}

.piece-s {
    background-color: #ff8c00;
}

.piece-t {
    background-color: #00e1ff;
}

.piece-z {
    background-color: #e3be58;
}

.piece-preview {
    background-color: rgb(255, 255, 255);
}

.left-panel {
    /* width: 88px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px 30px 10px 30px;
    margin: 5px 30px 10px 30px;
    ;
}

.right-panel {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px 5px 10px 5px;
    margin: 5px 30px 10px 30px;
}

.tetrix-wrapper {
    display: flex;
    flex-direction: row;
}

.h2 {
    font-size: medium;
    max-width: 200px;
}

.vasek {
    position: fixed;
    bottom: -40px;
    left: -70px;
}

.zdisa {
    position: fixed;
    bottom: -40px;
    right: -70px;
}

.center-flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.reset-button {
    color: white;
    background-color: rgb(0, 124, 182);
    padding: 10px;
    font-size: large;
    border: none;
    border-radius: 5px;
    margin: 4px;
    cursor: pointer;
}

.page-container {
    display: flex;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-weight: 300;
    width: 100%;
}

.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding: 0;
    margin: 0;
}

.game-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(254, 255, 191);
    border-radius: 15px;
    border: black solid 1px;
    z-index: 10000;
}

.container {
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-weight: 300;
    width: 100%;
    position: relative;
}